var $wrap = $('.g-wrap').eq(0);
var imgPath = "https://raw.githubusercontent.com/xinjialuo/xinjialuo.github.io/master/images/";

function addEventLisener() {

	$('body .g-img').on('click', function (e) {

		console.log('e', e.target);

		var dom = $(e.target);

		$wrap.show().find('.g-img-scale').css('background-image', dom.css('background-image'));
	});

	$('.g-btn-close, .g-wrap').on('click', function (e) {

		$wrap.hide();
	});
}

function appendList() {
	var tpl = "";

	_data2.default.forEach(function (itmes, index) {

		var oneImg = !itmes.img2 ? "singlePic" : "";

		tpl += '<li class="' + oneImg + '">\n\t\t            <div class="top-border"></div>\n\t\t            <ul class="g-imgs-box">\n\t\t        ' + (itmes.img1 ? '<div class="g-img" style="background-image: url(' + imgPath + itmes.img1 + ');"></div>' : "") + (itmes.img2 ? '<div class="g-img" style="background-image: url(' + imgPath + itmes.img2 + ');"></div>' : "") + (itmes.img3 ? '<div class="g-img" style="background-image: url(' + imgPath + itmes.img3 + ');"></div>' : "") + (itmes.img4 ? '<div class="g-img" style="background-image: url(' + imgPath + itmes.img4 + ');"></div>' : "") + '</ul>\n\t\t        </li>';
	});

	$('#g-list').append(tpl);
}

appendList();
addEventLisener();


